import React, { useState, useEffect, useRef, useReducer } from "react";
import { useSpring, animated } from "react-spring";
import gsap from "gsap";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Axios from "axios";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
} from "react-router-dom";
import Logo from "../Images/real_weeb_logo.png";
import emailRegex from "email-regex";
import { Canvas, useFrame } from "react-three-fiber";
import Register_Form from "./Register_Form";
import { AnimatePresence, motion } from "framer-motion/dist/framer-motion";
import "../Styles/Register.css";

const Register_Container = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [password2, setPassword2] = useState("");
  const [isLoading, setIsLoading] = useState("");
  const history = useHistory();

  const validate_user_email = () => {
    const regex_result = emailRegex({ exact: true }).test(email);
    return regex_result;
  };

  const registerUser = () => {
    if (validate_user_email() == false) {
      alert("the email address entered is not valid");
      return;
    }

    if (email == null || password == null || password2 == null) {
      alert(
        "Please make sure to fill out ALL fields prior to submitting. Thanks!"
      );
      return;
    }

    if (email == "" || password == "" || password2 == "") {
      alert(
        "Please make sure to fill out ALL fields prior to submitting. Thanks!"
      );
      return;
    } else if (password !== password2) {
      alert("The passwords submitted do not match. PLease try again");
    } else {
      setIsLoading(true);
      Axios.post("https://hero-uw.herokuapp.com/register", {
        email,
        password,
      }).then((res) => {
        alert(res.data.msg);
        setEmail("");
        setPassword("");
        setPassword2("");

        if (res.data.msg == "User name already exists") {
          setIsLoading(false);
          return;
        }
        return history.push("/Nicks_Guitar_Shop/Login");
      });
    }
  };

  useEffect(() => {
    gsap.from(".art-holder-cls", {
      opacity: 0,
      duration: 1.5,
      delay: 0.5,

      height: "0px",
    });

    gsap.from(".form_slide", {
      x: -100,
      duration: 1,
      opacity: 0,
      delay: 1.25,
    });
  }, []);

  return (
    <motion.div
      initial={{ opacity: 0, filter: "blur(100px)" }}
      animate={{ opacity: 1, filter: "blur(0px)" }}
      exit={{ opacity: 0, filter: "blur(100px)" }}
      transition={{ duration: 0.8 }}
    >
      <div id="register_container">
        <Register_Form
          emailProps={[email, setEmail]}
          passwordProps={[password, setPassword]}
          password2Props={[password2, setPassword2]}
          isLoadingProps={[isLoading, setIsLoading]}
          registerUserFunctionProp={registerUser}
        />
      </div>
    </motion.div>
  );
};

export default Register_Container;
