import React, { useState, useEffect, useRef, useReducer } from "react";
import { useSpring, animated } from "react-spring";
import gsap from "gsap";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Axios from "axios";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
} from "react-router-dom";
import Logo from "../Images/pngaaa.com-2325497.png";
import emailRegex from "email-regex";
import { Canvas, useFrame } from "react-three-fiber";
import "../Styles/Register.css";

function Box(props) {
  // This reference will give us direct access to the mesh
  const mesh = useRef();
  let rotationSpeed = 0.01;

  // Rotate mesh every frame, this is outside of React without overhead

  useFrame(() => {
    mesh.current.rotation.y += rotationSpeed;
  });

  return (
    <mesh {...props} ref={mesh}>
      <coneGeometry attach="geometry" args={[1.5, -1.85, 7]} />
      <meshBasicMaterial
        wireframe={true}
        metalness={1}
        attach="material"
        color={"green"}
      />
    </mesh>
  );
}

const curtain_call = () => {
  gsap.from(".art-holder-cls", {
    opacity: 0,
    duration: 1.5,
    delay: 0.5,
    height: "0px",
  });
};

const Register_Form = (props) => {
  const [email, setEmail] = props.emailProps;
  const [password, setPassword] = props.passwordProps;
  const registerUser = props.registerUserFunctionProp;
  const [password2, setPassword2] = props.password2Props;
  const [isLoading, setIsLoading] = props.isLoadingProps;

  return (
    <>
      <Form id="my_register_form" className="form_slide">
        <img id="reg_form_logo" src={Logo} />

        {isLoading && (
          <div id="loading_bar_holder2" className="loading_fade_in">
            <div id="Cube-3D-Container" className="Pyramid-Arrow">
              <h1 id="loading_text2">Loading....</h1>
              <Canvas>
                <ambientLight intensity={1} />
                <spotLight position={[0, 0, 0]} angle={0} />
                <Box position={[0, 0.6, 1.2]} />
              </Canvas>
            </div>
          </div>
        )}

        {!isLoading && (
          <div>
            <Form.Group
              className="mb-3"
              controlId="formBasicEmail"
              id="reg_form_info"
            >
              <Form.Label id="register_form_lbl">Email address</Form.Label>
              <Form.Control
                className="register_form_control"
                type="email"
                placeholder="Enter vaild email..."
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                onKeyDown={(event) => {
                  if (event.keyCode == 13) {
                    registerUser();
                  }
                }}
              />
              <Form.Text className="text-muted"></Form.Text>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label id="register_form_lbl2">Create Password</Form.Label>
              <Form.Control
                className="register_form_control"
                type="password"
                placeholder="Create Password..."
                value={password}
                onChange={(e) => {
                  setPassword(e.target.value);
                }}
                onKeyDown={(event) => {
                  if (event.keyCode == 13) {
                    registerUser();
                  }
                }}
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label id="register_form_lbl3">Re-Enter Password</Form.Label>
              <Form.Control
                className="register_form_control"
                type="password"
                placeholder="Confirm Password..."
                value={password2}
                onChange={(e) => {
                  setPassword2(e.target.value);
                }}
                onKeyDown={(event) => {
                  if (event.keyCode == 13) {
                    registerUser();
                  }
                }}
              />
            </Form.Group>

            <Button
              variant="outline-success"
              onClick={() => {
                registerUser();
              }}
            >
              Submit
            </Button>
          </div>
        )}
      </Form>
    </>
  );
};

export default Register_Form;
