import React, {
  useState,
  useEffect,
  useRef,
  useReducer,
  useContext,
} from "react";

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
} from "react-router-dom";

import gsap from "gsap";
import Axios from "axios";
import Forgot_PW_Modal_Container from "./Forgot_PW_Modal_Container";
import Promo_Request_Container from "./Promo_Request_Container";
import { loginAction } from "../REDUX/Actions";
import { setTokenAction } from "../REDUX/Actions";
import { useSelector, useDispatch } from "react-redux";
import { useSpring, animated } from "react-spring";
import Login_Form from "./Login_Form";
import { AnimatePresence, motion } from "framer-motion/dist/framer-motion";
import "../Styles/Login.css";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const userID = useSelector((state) => state.userID);
  const jwToken = useSelector((state) => state.jwToken);
  const [pwModal, setPWModal] = useState(false);
  const [promoModal, setPromoModal] = useState(false);
  const history = useHistory();
  const dispatch = useDispatch();

  const user_welcome = () => {
    alert(`Welcome ${jwToken} !!!!!`);
  };

  const closeModal = (modalType) => {
    if (modalType === "PW") {
      setPWModal(false);
    } else if (modalType === "Promo") {
      setPromoModal(false);
    }
  };

  const loginUser = () => {
    if (email == null || password == null) {
      alert(
        "Please make sure to enter both an email address AND a password that you have created. Thanks!"
      );
      return;
    } else if (email == "" || password == "") {
      alert(
        "Please make sure to enter both an email address AND a password that you have created. Thanks!"
      );
      return;
    } else {
      setIsLoading(true);
      Axios.post("https://hero-uw.herokuapp.com/login", {
        email,
        password,
      }).then((res) => {
        if (res.data.access_token) {
          dispatch(loginAction(res.data.username));
          dispatch(setTokenAction(res.data.access_token));
          return history.push("/Nicks_Guitar_Shop/Store");
        } else {
          setIsLoading(false);
          alert(res.data.msg);
        }
      });
    }
  };

  useEffect(() => {
    gsap.from(".form_slide", {
      x: -90,
      duration: 1,
      opacity: 0,
      delay: 1.25,
    });
  }, []);

  return (
    <motion.div
      initial={{ opacity: 0, filter: "blur(100px)" }}
      animate={{ opacity: 1, filter: "blur(0px)" }}
      exit={{ opacity: 0, filter: "blur(100px)" }}
      transition={{ duration: 1.25 }}
    >
      <div id="login_container">
        {promoModal && <Promo_Request_Container modalProp={closeModal} />}
        {pwModal && <Forgot_PW_Modal_Container modalProp={closeModal} />}

        <Login_Form
          isLoadingProp={isLoading}
          loginUserProp={loginUser}
          pwModalProp={pwModal}
          closePWModalProp={closeModal}
          emailProp={email}
          setEmailProp={setEmail}
          passwordProp={password}
          setPasswordProp={setPassword}
          setPWModalProp={setPWModal}
          setPromoModalProp={setPromoModal}
        />
      </div>
    </motion.div>
  );
};

export default Login;
