import React, { useEffect } from "react";
import closeButton from "../Images/close-btn.png";
import gsap from "gsap";

const Store_Ind_item_Modal = (props) => {
  const imageSrc = props.imageProp;
  const imagePrice = props.imagePriceProp;
  const imageDesc = props.descProp;
  const setModalVisible = props.setModalVisibleProp;

  const myStyle = {
    backgroundImage: `url(${imageSrc})`,
    backgroundSize: "contain",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
  };

  useEffect(() => {
    gsap.from(".myModal_cls", {
      opacity: 0,
      duration: 0.75,
      y: 75,
    });
  }, []);

  return (
    <div id="myModal" className="myModal_cls" style={myStyle}>
      <div id="modal_container_contrast">
        <img
          className="close"
          id="modal_closer"
          src={closeButton}
          onClick={() => {
            setModalVisible(false);
          }}
        ></img>
        <p id="guitar-price">${imagePrice}</p>
        <p id="modal-desc-txt">{imageDesc}</p>
      </div>
    </div>
  );
};

export default Store_Ind_item_Modal;
