import React, {
  useState,
  useEffect,
  useRef,
  useReducer,
  useContext,
} from "react";
import { useSpring, animated } from "react-spring";
import Button from "react-bootstrap/Button";
import closeButton from "../Images/close-btn.png";
import { useSelector, useDispatch } from "react-redux";
import { updateCartAction } from "../REDUX/Actions";
import { updateTotalAction } from "../REDUX/Actions";
import { updateTaxAction } from "../REDUX/Actions";

const Shopping_Cart_Container = (props) => {
  const dispatch = useDispatch();
  const cartToggle = props.cartToggleProp;
  const cart = props.cartProp;
  const submitCart = props.submitCartProp;
  const setFirstName = props.setFirstNameProp;
  const setLastName = props.setLastNameProp;
  const total = props.totalProp;
  const clear_cart = props.clear_cartProp;
  const cartHideText = props.cartHideTextProp;
  const setEmail = props.setEmailProp;
  const setCart = props.setCartProp;
  const setTotal = props.setTotalProp;
  const tax = useSelector((state) => state.tax);
  const userID = useSelector((state) => state.userID);

  return (
    <div id="shopping-cart-holder">
      <div id="cart_hide_holder">
        <button
          type="button"
          id="cart_hide"
          className="cart-hider"
          onClick={() => cartToggle()}
        >
          {cartHideText} ({cart.length} Items)
        </button>
      </div>
      <div id="myCart" className="cart_hider">
        <div id="login_ID_holder">
          <p>
            <span id="login_ID_lbl"> Current Login: </span> {userID}
          </p>
        </div>
        <input
          className="custName_holder1"
          type="text"
          placeholder="Enter First Name"
          onChange={(e) => {
            setFirstName(e.target.value);
          }}
        ></input>
        <input
          className="custName_holder2"
          type="text"
          placeholder="Enter Last Name"
          onChange={(e) => {
            setLastName(e.target.value);
          }}
        ></input>

        <p id="cart_price">
          Subtotal: <span className="subtotal_txt"> ${total} </span>
        </p>
        <p id="cart_tax">
          Tax: <span className="tax_txt"> ${tax} </span>
        </p>

        {cart.map((item, index) => (
          <div className="cart_fade" key={index}>
            <img
              id="remove_item_btn"
              src={closeButton}
              onClick={() => {
                var delete_item = window.confirm(
                  "This will delete the item selected. Are you sure?"
                );

                if (delete_item === true) {
                  item.index = index;
                  dispatch(
                    updateCartAction(
                      cart.filter((item) => item.index !== index)
                    )
                  );
                  console.log(cart);
                  const new_total =
                    parseFloat(total) - parseFloat(item.Price) * item.item_Qty;
                  const new_tax_total =
                    parseFloat(tax) -
                    parseFloat(item.Price * item.item_Qty * 0.06);
                  dispatch(updateTotalAction(new_total.toFixed(2)));
                  dispatch(updateTaxAction(new_tax_total.toFixed(2)));
                } else {
                  return;
                }
              }}
            ></img>
            <p id="apparel_title2">{item.Title}</p>
            <p id="apparel_price2">{item.Price} Each</p>
            <p id="apparel_price2">(Qty = {item.item_Qty}) </p>
          </div>
        ))}
        <Button
          variant="danger"
          size="sm"
          id="clear_cart_btn"
          onClick={() => clear_cart()}
        >
          Clear
        </Button>

        <Button
          variant="success"
          size="sm"
          id="checkout_btn"
          onClick={() => submitCart(cart, total)}
        >
          Checkout
        </Button>
        <p id="email_note">
          Please Note: A confirmation email will be sent to the user email
          address provided.
        </p>
      </div>
    </div>
  );
};

export default Shopping_Cart_Container;
