import React, {
  useState,
  useEffect,
  useRef,
  useReducer,
  useContext,
} from "react";

import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";

import { Canvas, useFrame } from "react-three-fiber";
import { useSpring, animated } from "react-spring";
import Logo from "../Images/pngaaa.com-2325497.png";

function Box(props) {
  // This reference will give us direct access to the mesh
  const mesh = useRef();
  let rotationSpeed = 0.01;

  // Rotate mesh every frame, this is outside of React without overhead

  useFrame(() => {
    mesh.current.rotation.y += rotationSpeed;
  });

  return (
    <mesh {...props} ref={mesh}>
      <coneGeometry attach="geometry" args={[1.5, -1.85, 7]} />
      <meshBasicMaterial
        wireframe={true}
        metalness={1}
        attach="material"
        color={"green"}
      />
    </mesh>
  );
}

const Login_View = (props) => {
  const isLoading = props.isLoadingProp;
  const loginUser = props.loginUserProp;
  const pwModal = props.pwModalProp;
  const setPromoModal = props.setPromoModalProp;
  const closePWModal = props.closePWModalProp;
  const email = props.emailProp;
  const setEmail = props.setEmailProp;
  const password = props.passwordProp;
  const setPassword = props.setPasswordProp;
  const setPWModal = props.setPWModalProp;

  useEffect(() => {
    if (email === "Guest101@test.com" && password === "password") {
      loginUser();
    }
  }, [email, password]);

  return (
    <>
      <Form id="my_login_form" className="form_slide">
        <img id="reg_form_logo" src={Logo} />
        {isLoading && (
          <div id="loading_bar_holder2" className="loading_fade_in">
            <div id="Cube-3D-Container" className="Pyramid-Arrow">
              <h1 id="loading_text2">Loading....</h1>
              <Canvas>
                <ambientLight intensity={1} />
                <spotLight position={[0, 0, 0]} angle={0} />
                <Box position={[0, 0.6, 1.2]} />
              </Canvas>
            </div>
          </div>
        )}
        {!isLoading && (
          <div>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label id="login_form_lbl">Email address</Form.Label>
              <Form.Control
                className="register_form_control"
                type="email"
                placeholder="Enter vaild email..."
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                onKeyDown={(event) => {
                  if (event.keyCode == 13) {
                    loginUser();
                  }
                }}
              />
              <Form.Text className="text-muted"></Form.Text>
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label id="login_form_lbl2">Password</Form.Label>
              <Form.Control
                className="register_form_control"
                type="password"
                placeholder="Enter Password..."
                value={password}
                onChange={(e) => {
                  setPassword(e.target.value);
                }}
                onKeyDown={(event) => {
                  if (event.keyCode == 13) {
                    loginUser();
                  }
                }}
              />
            </Form.Group>

            <Button
              variant="outline-success"
              id="login_btn"
              onClick={(e) => {
                e.preventDefault();
                loginUser();
              }}
            >
              Submit
            </Button>

            <div id="login_form_lower">
              <p
                className="api_input1"
                onClick={() => {
                  setEmail("Guest101@test.com");
                  setPassword("password");
                }}
              >
                ~ GUEST LOGIN LINK ~
              </p>

              <div id="promo_link_holder">
                <p class="promo_link_txt " onClick={() => setPromoModal(true)}>
                  -Get Holiday Sale Email-
                </p>
              </div>

              <div>
                <p id="forgot_pw_text" onClick={() => setPWModal(true)}>
                  Forgot Password?
                </p>
              </div>
            </div>
          </div>
        )}
      </Form>
    </>
  );
};

export default Login_View;
