import React from "react";

import Axios from "axios";
import Promo_Request_View from "./Promo_Request_View";
import "../Styles/Login.css";
import gsap from "gsap";

class Promo_Request_Container extends React.Component {
  constructor(props) {
    super();
    this.close_modal = props.modalProp;

    this.state = {
      email: "",
      verification_code_value: "",
      change_pw_inputs_visible: false,
    };
  }

  get_mjml_email = function (email, v_code) {
    Axios.post(`https://hero-uw.herokuapp.com/send_mjml_email`, {
      email,
      v_code,
    }).then((res) => {
      alert(res.data);
    });
  };

  componentDidMount() {
    gsap.from(".promo_modal_cls", {
      x: -90,
      duration: 1,
      opacity: 0,
    });
  }

  render() {
    return (
      <Promo_Request_View
        email_Prop={this.state.email}
        verification_code_value_Prop={this.state.verification_code_value}
        change_pw_inputs_visible_Prop={this.state.change_pw_inputs_visible_Prop}
        modalProp={this.close_modal}
        mjmlFuncProp={this.get_mjml_email}
      />
    );
  }
}

export default Promo_Request_Container;
