import tax_Reducer from "../Reducers/tax_Reducer";
import total_Reducer from "../Reducers/total_Reducer";

export const loginAction = (userID) => {
  return {
    type: "LOGGED_IN",
    payload: userID,
  };
};

export const logoutAction = () => {
  return {
    type: "LOGGED_OUT",
  };
};

export const setTokenAction = (token) => {
  return {
    type: "SET_TOKEN",
    payload: token,
  };
};

export const clearTokenAction = () => {
  return {
    type: "CLEAR_TOKEN",
  };
};

export const updateCartAction = (cart) => {
  return {
    type: "UPDATE_CART",
    payload: cart,
  };
};

export const clearCartAction = () => {
  return {
    type: "CLEAR_CART",
  };
};

export const updateTotalAction = (total) => {
  return {
    type: "UPDATE_TOTAL",
    payload: total,
  };
};

export const clearTotalAction = () => {
  return {
    type: "CLEAR_TOTAL",
  };
};

export const updateTaxAction = (tax) => {
  return {
    type: "UPDATE_TAX",
    payload: tax,
  };
};

export const clearTaxAction = () => {
  return {
    type: "CLEAR_TAX",
  };
};
