import "./App.css";
import React, { useState, useEffect, useRef, useReducer } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
  useLocation,
} from "react-router-dom";
import Login_Container from "./Components/Login_Container";
import Nav from "./Components/Nav";
import Gear_Demos_Container from "./Components/Gear_Demos_Container";

import Store_Access_Container from "./Components/Store_Access_Container";
import { AnimatePresence, motion } from "framer-motion/dist/framer-motion";
import Weeb_Logo from "./Weeb_Logo";
import gsap from "gsap";
import Register_Container from "./Components/Register_Container";

function App() {
  const [page, setPage] = useState("home");
  const [jwtToken, setJwtToken] = useState(null);
  const [userID, setUserID] = useState(null);
  const location = useLocation();

  const pageTurner = () => {
    setPage("home");
  };

  useEffect(() => {
    gsap.from(".nav_hover", {
      x: -900,
      stagger: 0.7,
      duration: 1.8,
    });
    /*
    setTimeout(pageTurner, 9500);
    */
    console.log(location);
  }, []);

  useEffect(() => {
    if (window.screen.width > 2000) {
      document.body.style.zoom = "137.5%";
    }
  }, []);

  return (
    <div className="App">
      <Nav pageProp={setPage} />

      <div id="main-container">
        <AnimatePresence exitBeforeEnter>
          <Switch location={location} key={location.pathname}>
            <Route exact path="/Nicks_Guitar_Shop">
              {jwtToken ? (
                <Redirect to="/Nicks_Guitar_Shop/Store" />
              ) : (
                <Login_Container />
              )}
            </Route>
            <Route
              path="/Nicks_Guitar_Shop/Login"
              component={Login_Container}
            ></Route>
            <Route
              path="/Nicks_Guitar_Shop/Store"
              component={Store_Access_Container}
            ></Route>
            <Route
              path="/Nicks_Guitar_Shop/About"
              component={Gear_Demos_Container}
            ></Route>
            <Route
              path="/Nicks_Guitar_Shop/Register"
              component={Register_Container}
            ></Route>
          </Switch>
        </AnimatePresence>
      </div>
    </div>
  );
}

export default App;
