import React, {
  useState,
  useEffect,
  useRef,
  useReducer,
  useContext,
} from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
} from "react-router-dom";
import { useSpring, animated } from "react-spring";
import Logo from "../Images/pngaaa.com-162083.png";
import { Link } from "react-router-dom";
import "../Styles/Nav.css";
import { useSelector, useDispatch } from "react-redux";
import { logoutAction, clearTokenAction } from "../REDUX/Actions";

const Nav = (props) => {
  const jwToken = useSelector((state) => state.jwToken);
  const setPage = props.pageProp;
  const props2 = useSpring({
    config: { friction: 50, damping: 0.2 },
    from: { opacity: 0, transform: "translateY(1000px)" },
    to: { opacity: 1, transform: "translateY(0px)" },
  });

  const history = useHistory();
  const dispatch = useDispatch();

  return (
    <div id="Nav-Bar">
      <ul id="Nav-List">
        <li>
          <img id="store-logo" src={Logo}></img>{" "}
        </li>
        <li>
          <h1 id="shop-logo-txt"> Nick's Guitar Shop</h1>
        </li>

        {!jwToken && (
          <Link id="linkster" to="/Nicks_Guitar_Shop/Register">
            <li id="Nav-Item" className="nav_hover">
              REGISTER
            </li>
          </Link>
        )}

        {!jwToken && (
          <Link id="linkster" to="/Nicks_Guitar_Shop/Login">
            <li id="Nav-Item" className="nav_hover">
              LOGIN
            </li>
          </Link>
        )}

        {jwToken && (
          <Link id="linkster" to="/Nicks_Guitar_Shop/Store">
            <li id="Nav-Item" className="nav_hover">
              GUITARS
            </li>
          </Link>
        )}

        {jwToken && (
          <Link id="linkster" to="/Nicks_Guitar_Shop/About">
            <li id="Nav-Item" className="nav_hover">
              DEMOS
            </li>
          </Link>
        )}

        {jwToken && (
          <li
            id="Nav-Item"
            className="nav_logout"
            onClick={() => {
              dispatch(clearTokenAction());
              dispatch(logoutAction());
              return history.push("/Nicks_Guitar_Shop/Login");
            }}
          >
            LOGOUT
          </li>
        )}
      </ul>
    </div>
  );
};

export default Nav;
