import React from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import closeButton from "../Images/close-btn.png";
import "../Styles/Login.css";

class Promo_Request_View extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: props.email_Prop,
      is_verification_code: props.is_verification_code_Prop,
      verification_code_value: props.verification_code_value_Prop,
      change_pw_inputs_visible: props.change_pw_inputs_visible_Prop,
    };
    this.create_verification_code = props.create_verification_code_Prop;
    this.close_modal = props.modalProp;
    this.check_verification_code = props.check_verification_code_Prop;
    this.get_mjml_email = props.mjmlFuncProp;
  }

  render() {
    return (
      <div id="Promo_Request_Modal" className="promo_modal_cls">
        <img
          className="pw_modal_close"
          id="modal_closer"
          src={closeButton}
          onClick={() => {
            this.close_modal("Promo");
          }}
        ></img>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label id="login_form_lbl">Email address</Form.Label>
          <Form.Control
            className="register_form_control"
            type="email"
            placeholder="Enter vaild email..."
            value={this.state.email}
            onChange={(e) => {
              this.setState({ email: e.target.value });
            }}
            onKeyDown={(event) => {
              if (event.keyCode == 13) {
                alert(`your verification code is ${this.state.email}!! `);
              }
            }}
          />
          <Form.Text className="text-muted"></Form.Text>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label id="login_form_lbl">Enter Code</Form.Label>
          <Form.Control
            className="register_form_control"
            type="password"
            placeholder="Enter Verification Code..."
            value={this.state.verification_code_value}
            onChange={(e) => {
              this.setState({ verification_code_value: e.target.value });
            }}
            onKeyDown={(event) => {
              if (event.keyCode == 13) {
              }
            }}
          />
        </Form.Group>
        <Button
          id="forgot_pw_modal_btn"
          variant="primary"
          onClick={(e) => {
            e.preventDefault();
            console.log(this.state.email);
            this.get_mjml_email(
              this.state.email,
              this.state.verification_code_value
            );
          }}
        >
          Send Promo Email
        </Button>
      </div>
    );
  }
}

export default Promo_Request_View;
