const Gear_Demo_Individual = (props) => {
  return (
    <iframe
      src={props.srcProp}
      className="Gear_YT_Vid"
      title="YouTube video player"
      frameborder="0"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
      allowfullscreen
    ></iframe>
  );
};

export default Gear_Demo_Individual;
