import React from "react";

import Axios from "axios";
import Forgot_PW_Modal_View from "./Forgot_PW_Modal_View";
import "../Styles/Login.css";
import gsap from "gsap";

class Forgot_PW_Modal_Container extends React.Component {
  constructor(props) {
    super();
    this.close_modal = props.modalProp;

    this.state = {
      email: "",
      is_verification_code: false,
      verification_code_value: "",
      change_pw_inputs_visible: false,
      password: "",
      password2: "",
    };
  }

  create_verification_code = function (email) {
    Axios.post(`https://hero-uw.herokuapp.com/get_verification_code`, {
      email,
    }).then((res) => {
      if (res.data !== "this user does NOT exist") {
        this.setState({ is_verification_code: true });
      } else alert(res.data);
    });
  };

  check_verification_code = function (verification_code) {
    Axios.post(`https://hero-uw.herokuapp.com/check_verification_code`, {
      verification_code,
    }).then((res) => {
      if (res.data.verification_code_valid) {
        this.setState({ change_pw_inputs_visible: true });
      }
    });
  };

  change_password = function (password, password2, email) {
    if (email == null || password == null || password2 == null) {
      alert(
        "Please make sure to fill out ALL fields prior to submitting. Thanks!"
      );
      return;
    } else if (email == "" || password == "" || password2 == "") {
      alert(
        "Please make sure to fill out ALL fields prior to submitting. Thanks!"
      );
      return;
    } else if (password !== password2) {
      alert("The passwords submitted do not match. PLease try again");
    } else {
      Axios.post("https://hero-uw.herokuapp.com/change_password", {
        password,
        email,
      }).then((res) => {
        alert(res.data);
        this.setState({
          password: "",
          password2: "",
          email: "",
          verification_code_value: "",
        });
        this.close_modal();
      });
    }
  };

  componentDidMount() {
    gsap.from(".myModal_cls", {
      x: -90,
      duration: 1,
      opacity: 0,
    });
  }

  render() {
    return (
      <Forgot_PW_Modal_View
        email_Prop={this.state.email}
        is_verification_code_Prop={this.state.is_verification_code}
        verification_code_value_Prop={this.state.verification_code_value}
        change_pw_inputs_visible_Prop={this.state.change_pw_inputs_visible_Prop}
        password_Prop={this.state.password}
        password2_Prop={this.state.password2}
        create_verification_code_Prop={this.create_verification_code}
        check_verification_code_Prop={this.check_verification_code}
        change_password_Prop={this.change_password}
        modalProp={this.close_modal}
      />
    );
  }
}

export default Forgot_PW_Modal_Container;
