import React, { useState, useEffect, useRef, useReducer } from "react";
import { useSpring, animated } from "react-spring";
import closeButton from "../Images/close-btn.png";
import gsap from "gsap";
import Button from "react-bootstrap/Button";
import { Canvas, useFrame } from "react-three-fiber";
import Store_Ind_item_Modal from "./Store_Ind_Item_Modal";
import Store_Individual_Item from "./Store_Individual_Item";
import { AnimatePresence, motion } from "framer-motion/dist/framer-motion";
import Axios from "axios";
Axios.defaults.withCredentials = true;

function Box(props) {
  // This reference will give us direct access to the mesh
  const mesh = useRef();
  let rotationSpeed = 0.01;

  // Rotate mesh every frame, this is outside of React without overhead

  useFrame(() => {
    mesh.current.rotation.y += rotationSpeed;
  });

  return (
    <mesh {...props} ref={mesh}>
      <coneGeometry attach="geometry" args={[2, -3.5, 7]} />
      <meshBasicMaterial
        wireframe={true}
        metalness={1}
        attach="material"
        color={"green"}
      />
    </mesh>
  );
}

const Store_Access_Items_View = (props) => {
  let apparel_type = useRef();

  const itemArray = props.itemArrayProp;
  const setItemQty = props.setItemQtyProp;
  const addToCart = props.addToCartProp;
  const isLoading = props.isLoadingProp;
  const get_Guitar_Images = props.get_Guitar_Images_Prop;
  const get_Bass_Images = props.get_Bass_Images_Prop;
  const isStoreHidden = props.isStoreHiddenProp;
  const [modalVisible, setModalVisible] = useState(false);
  const [currentItemImage, setCurrentItemImage] = useState(null);
  const [currentItemPrice, setCurrentItemPrice] = useState(null);
  const [currentItemDesc, setCurrentItemDesc] = useState(null);

  return (
    <>
      {!isStoreHidden && (
        <div id="main_merch_container" class="main_merch_container">
          <div id="loading_bar_holder" className="loading_fade_in">
            {isLoading && (
              <div>
                <div id="Cube-3D-Container" className="Pyramid-Arrow">
                  <Canvas>
                    <ambientLight intensity={1} />
                    <spotLight position={[0, 0, 0]} angle={0} />
                    <Box position={[0, -1, 1.2]} />
                  </Canvas>
                </div>
                <h1 id="loading_text">Loading....</h1>
              </div>
            )}
          </div>

          {!isLoading && (
            <animated.div id="shop_container" className="item_container_shift">
              {modalVisible && (
                <Store_Ind_item_Modal
                  imageProp={currentItemImage}
                  imagePriceProp={currentItemPrice}
                  setModalVisibleProp={setModalVisible}
                  descProp={currentItemDesc}
                />
              )}

              <div id="ind_items_holder" className="ind-items-holder-cls">
                <div id="clothing_type_selector">
                  <select
                    name="selectStock"
                    id="apparel_type_selector"
                    ref={apparel_type}
                  >
                    <option value="Guitars">6-String Guitars</option>
                    <option value="Bass">Bass Guitars</option>
                  </select>

                  <Button
                    id="tshirts_btn"
                    variant="outline-primary"
                    onClick={() => {
                      if (apparel_type.current.value == "Guitars") {
                        get_Guitar_Images();
                      } else if (apparel_type.current.value == "Bass") {
                        get_Bass_Images();
                      }
                    }}
                  >
                    Update
                  </Button>
                </div>

                {itemArray.map((artPiece, index) => (
                  <Store_Individual_Item
                    imageProp={artPiece.Image}
                    priceProp={artPiece.Price}
                    titleProp={artPiece.Title}
                    descProp={artPiece.Desc}
                    setCurrentItemImageProp={setCurrentItemImage}
                    setCurrentItemPriceProp={setCurrentItemPrice}
                    setCurrentItemDescProp={setCurrentItemDesc}
                    this_Item_Prop={artPiece}
                    index_Prop={index}
                    setItemQtyProp={setItemQty}
                    setModalVisibleProp={setModalVisible}
                    addToCartProp={addToCart}
                    key={index}
                  />
                ))}
              </div>
            </animated.div>
          )}
        </div>
      )}
    </>
  );
};

export default Store_Access_Items_View;
