import React, {
  useState,
  useEffect,
  useRef,
  useReducer,
  useContext,
} from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
} from "react-router-dom";
import { useSpring, animated } from "react-spring";
import { AnimatePresence, motion } from "framer-motion/dist/framer-motion";
import gsap from "gsap";
import Axios from "axios";
import { clearTokenAction } from "../REDUX/Actions";
import { useSelector, useDispatch } from "react-redux";
import Gear_Demo_Individual from "./Gear_Demo_Individual";
Axios.defaults.withCredentials = true;

const Gear_Demos = () => {
  const [artArray, setArtArray] = useState([]);
  const history = useHistory();
  const jwToken = useSelector((state) => state.jwToken);
  const dispatch = useDispatch();

  const My_Vids = [
    { src: "https://www.youtube.com/embed/cI8Ih23ANJI" },
    { src: "https://www.youtube.com/embed/Ni9T5jFCtTM" },
    { src: "https://www.youtube.com/embed/58NS85cxZEc" },
    { src: "https://www.youtube.com/embed/CuRO6Fh5wek" },
  ];

  const props2 = useSpring({
    config: { duration: 500 },
    from: { opacity: 0 },
    to: { opacity: 1 },
  });

  const login_check = async () => {
    try {
      let login_response = await Axios.get(
        `https://hero-uw.herokuapp.com/login_check`,
        {
          headers: {
            Authorization: jwToken,
          },
        }
      );

      let login_value = login_response.data;
      console.log(login_value);
      if (login_value == false) {
        dispatch(clearTokenAction());

        return history.push("/uw/Login");
      }
    } catch (err) {
      console.log("Error:" + err);
    }
  };

  useEffect(() => {
    gsap.from(".art-holder-cls", {
      opacity: 0,
      duration: 1.5,
      delay: 0.5,
    });

    login_check();
  }, []);

  return (
    <motion.div
      initial={{ opacity: 0, filter: "blur(100px)" }}
      animate={{ opacity: 1, filter: "blur(0px)" }}
      exit={{ opacity: 0, filter: "blur(100px)" }}
      transition={{ duration: 0.8 }}
    >
      <animated.div id="yt_vid_container" style={props2}>
        <div id="yt_vid_gallery" class="art-holder-cls">
          {My_Vids.map((video, index) => (
            <Gear_Demo_Individual srcProp={video.src} />
          ))}
        </div>
      </animated.div>
    </motion.div>
  );
};

export default Gear_Demos;
