import React from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import closeButton from "../Images/close-btn.png";
import "../Styles/Login.css";

class Forgot_PW_Modal_View extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: props.email_Prop,
      is_verification_code: props.is_verification_code_Prop,
      verification_code_value: props.verification_code_value_Prop,
      change_pw_inputs_visible: props.change_pw_inputs_visible_Prop,
      password: props.password_Prop,
      password2: props.password2_Prop,
    };
    this.create_verification_code = props.create_verification_code_Prop;
    this.close_modal = props.modalProp;
    this.check_verification_code = props.check_verification_code_Prop;
    this.change_password = props.change_password_Prop;
  }

  render() {
    return (
      <div id="forgotPwModal" className="myModal_cls">
        <img
          className="pw_modal_close"
          id="modal_closer"
          src={closeButton}
          onClick={() => {
            this.close_modal("PW");
          }}
        ></img>

        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label id="login_form_lbl">Email address</Form.Label>
          <Form.Control
            className="register_form_control"
            type="email"
            placeholder="Enter vaild email..."
            value={this.state.email}
            onChange={(e) => {
              this.setState({ email: e.target.value });
            }}
            onKeyDown={(event) => {
              if (event.keyCode == 13) {
                alert(`your verification code is ${this.state.email}!! `);
              }
            }}
          />
          <Form.Text className="text-muted"></Form.Text>
        </Form.Group>

        {this.state.is_verification_code && (
          <div>
            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label id="login_form_lbl">Verification Code</Form.Label>
              <Form.Control
                className="register_form_control"
                type="password"
                placeholder="Enter Verification Code..."
                value={this.state.verification_code_value}
                onChange={(e) => {
                  this.setState({ verification_code_value: e.target.value });
                }}
                onKeyDown={(event) => {
                  if (event.keyCode == 13) {
                  }
                }}
              />
            </Form.Group>

            {!this.state.change_pw_inputs_visible && (
              <Button
                id="check_verification_code_btn"
                variant="primary"
                onClick={(e) => {
                  e.preventDefault();
                  this.check_verification_code(
                    this.state.verification_code_value
                  );
                }}
              >
                Check Verification_Code
              </Button>
            )}

            {this.state.change_pw_inputs_visible && (
              <div>
                <Form.Group className="mb-3" controlId="formBasicPassword">
                  <Form.Label id="register_form_lbl">
                    Create New Password
                  </Form.Label>
                  <Form.Control
                    className="register_form_control"
                    type="password"
                    placeholder="Create Password..."
                    value={this.state.password}
                    onChange={(e) => {
                      this.setState({ password: e.target.value });
                    }}
                    onKeyDown={(event) => {
                      if (event.keyCode == 13) {
                      }
                    }}
                  />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">
                  <Form.Label id="register_form_lbl">
                    Re-Enter New Password
                  </Form.Label>
                  <Form.Control
                    className="register_form_control"
                    type="password"
                    placeholder="Confirm Password..."
                    value={this.state.password2}
                    onChange={(e) => {
                      this.setState({ password2: e.target.value });
                    }}
                    onKeyDown={(event) => {
                      if (event.keyCode == 13) {
                      }
                    }}
                  />
                </Form.Group>

                <Button
                  id="change_pw_btn"
                  variant="primary"
                  onClick={(e) => {
                    e.preventDefault();
                    this.change_password(
                      this.state.password,
                      this.state.password2,
                      this.state.email
                    );
                  }}
                >
                  Change Password
                </Button>
              </div>
            )}
          </div>
        )}
        {!this.state.is_verification_code && (
          <Button
            id="forgot_pw_modal_btn"
            variant="primary"
            onClick={(e) => {
              e.preventDefault();
              console.log(this.state.email);
              this.create_verification_code(this.state.email);
            }}
          >
            Send Reset Code
          </Button>
        )}
      </div>
    );
  }
}

export default Forgot_PW_Modal_View;
