import React, {
  useState,
  useEffect,
  useRef,
  useReducer,
  useContext,
} from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
} from "react-router-dom";
import Button from "react-bootstrap/Button";
import { useSpring, animated } from "react-spring";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Store_Access_Items_View from "./Store_Access_Items_View";
import Store_Shopping_Cart_Container from "./Store_Shopping_Cart_Container";
import Axios from "axios";
import { AnimatePresence, motion } from "framer-motion/dist/framer-motion";
import { clearTokenAction, updateTotalAction } from "../REDUX/Actions";
import { clearCartAction } from "../REDUX/Actions";
import { clearTotalAction } from "../REDUX/Actions";
import { updateCartAction } from "../REDUX/Actions";
import { updateTaxAction, clearTaxAction } from "../REDUX/Actions";
import { useSelector, useDispatch } from "react-redux";
import "../Styles/Store.css";

Axios.defaults.withCredentials = true;
gsap.registerPlugin(ScrollTrigger);
gsap.core.globals("ScrollTrigger", ScrollTrigger);

const Store_Access_Container = () => {
  let apparel_type = useRef();

  const [firstname, setFirstName] = useState(null);
  const [lastname, setLastName] = useState(null);
  const [itemQty, setItemQty] = useState(null);
  const [isCartHidden, setIsCartHidden] = useState(false);
  const [cartHideText, setCartHideText] = useState("Show Cart");
  const [isLoading, setIsLoading] = useState(true);
  const [isStoreHidden, setIsStoreHidden] = useState(false);
  const [itemArray, setItemArray] = useState([]);
  const [newOrder, setNewOrder] = useState([]);
  const history = useHistory();
  const userID = useSelector((state) => state.userID);
  const jwToken = useSelector((state) => state.jwToken);
  const cart = useSelector((state) => state.cart);
  const total = useSelector((state) => state.total);
  const dispatch = useDispatch();

  const get_Guitar_Images = () => {
    Axios.get(`https://hero-uw.herokuapp.com/get_guitars`).then((res) => {
      setIsLoading(false);
      setItemArray(res.data);
    });
  };

  const get_Bass_Images = () => {
    Axios.get(`https://hero-uw.herokuapp.com/get_bass`).then((res) => {
      setIsLoading(false);
      setItemArray(res.data);
    });
  };

  const submitCart = (items, total) => {
    if (lastname == null || firstname == null) {
      alert(
        "Please enter a first AND last name before submitting your cart. Thanks!"
      );
    } else if (userID == "Guest101@test.com") {
      alert(
        "Checkout functionality is not available for Guest Logins. Thanks again!"
      );
    } else {
      items.map((item) => {
        item.firstname = firstname;
        item.lastname = lastname;
        item.total = total;
        item.email = userID;
      });

      console.log(total);

      const item_string = JSON.stringify(items);

      Axios.post("https://hero-uw.herokuapp.com/create", {
        item_string,
      });
      alert(
        `Your UrbanWeeb order for ${total} has now been submitted, ${firstname} ${lastname}`
      );

      clear_cart();
    }
  };

  const clear_cart = (product) => {
    dispatch(clearCartAction());
    dispatch(clearTotalAction());
    dispatch(clearTaxAction());
  };
  useEffect(() => {
    if (total !== 0) {
      gsap.to(".cart_fade", { y: 10, duration: 1 });
    }
  }, [total]);

  useEffect(() => {
    gsap.from(".apparel_fade", {
      duration: 1,
      opacity: 0,

      delay: 0.75,
    });
  }, [itemArray]);

  const cartToggle = () => {
    const myWidth = document.getElementById(
      "Guitar_Store_Container"
    ).clientWidth;
    if (cartHideText == "Hide Cart") {
      setIsCartHidden(true);
      gsap.to(".cart_hider", { opacity: 0, duration: 0.65, x: 140 });
      setCartHideText("Show Cart");
      if (myWidth < 420) {
        setIsStoreHidden(false);
      }
    } else {
      setIsCartHidden(false);

      if (myWidth < 420) {
        gsap.to(".ind-items-holder-cls", {
          opacity: 0,
          duration: 0.5,
        });
        setTimeout(() => {
          setIsStoreHidden(true);
          gsap.to(".main_merch_container", {
            opacity: 0,
            y: -300,
            duration: 1,
          });
        }, 1000);
      }

      if (myWidth < 500) {
        gsap.to(".cart_hider", {
          opacity: 1,
          duration: 0.65,
          x: -140,
        });
      } else if (myWidth > 1500) {
        gsap.to(".cart_hider", {
          opacity: 1,
          duration: 0.65,
          x: 0,
        });
      } else {
        gsap.to(".cart_hider", {
          opacity: 1,
          duration: 0.65,
          x: -42.5,
        });
      }

      setCartHideText("Hide Cart");
    }
  };

  useEffect(() => {
    gsap.to(".cart_hider", { opacity: 0, duration: 0.5 });
    Axios.get(`https://hero-uw.herokuapp.com/login_check`, {
      headers: {
        Authorization: jwToken,
      },
    }).then((res) => {
      if (res.data == false) {
        dispatch(clearTokenAction());
        return history.push("/uw/Login");
      }
    });
  }, []);

  const addToCart = (product) => {
    product.item_Qty = itemQty;
    product.date_time = "NA";

    if (itemQty == null) {
      alert(
        "Please choose quantity for your item before adding to the cart. Thanks again!"
      );
    } else {
      dispatch(
        updateCartAction([
          ...cart,
          {
            Title: product.Title,
            Price: product.Price,
            item_Qty: itemQty,
          },
        ])
      );

      cart.forEach((item) => {
        if (item.Title === product.Title) {
          item.item_Qty =
            parseFloat(item.item_Qty) + parseFloat(product.item_Qty);
          product.duplicate = true;
          dispatch(
            updateCartAction(
              cart.filter((product) => product.duplicate !== true)
            )
          );
        }
      });

      console.log(cart);

      const this_price = product.Price;
      const new_total = (
        parseFloat(total) +
        parseFloat(this_price) * product.item_Qty
      ).toFixed(2);
      const new_tax_total = parseFloat(new_total * 0.06);
      dispatch(updateTotalAction(new_total));
      dispatch(updateTaxAction(new_tax_total.toFixed(2)));
      gsap.from(".subtotal_txt", {
        color: "rgb(235, 85, 15)",
        duration: 2,
        fontSize: "1.5rem",
      });

      gsap.from(".tax_txt", {
        color: "rgb(235, 85, 15)",
        duration: 2,
      });

      gsap.from(".cart-hider", {
        color: "orange",
        duration: 1,
      });
      setItemQty(null);
    }

    const clear_sizes = document.querySelectorAll(".item_size_input");
    clear_sizes.forEach((item) => (item.selectedIndex = 0));
  };

  useEffect(() => {
    get_Guitar_Images();

    gsap.to(".Guitar_Store_Container_Cls", {
      opacity: 1,
      duration: 3,
    });
  }, []);

  return (
    <div id="Guitar_Store_Container" class="Guitar_Store_Container_Cls">
      <Store_Access_Items_View
        isLoadingProp={isLoading}
        setItemQtyProp={setItemQty}
        itemArrayProp={itemArray}
        isCartHiddenProp={isCartHidden}
        setIsCartHiddenProp={setIsCartHidden}
        isStoreHiddenProp={isStoreHidden}
        addToCartProp={addToCart}
        get_Guitar_Images_Prop={get_Guitar_Images}
        get_Bass_Images_Prop={get_Bass_Images}
      />

      <Store_Shopping_Cart_Container
        cartToggleProp={cartToggle}
        cartProp={cart}
        submitCartProp={submitCart}
        setFirstNameProp={setFirstName}
        setLastNameProp={setLastName}
        totalProp={total}
        clear_cartProp={clear_cart}
        cartHideTextProp={cartHideText}
      />
    </div>
  );
};

export default Store_Access_Container;
