import gsap from "gsap";
import Button from "react-bootstrap/Button";
import ReactStars from "react-rating-stars-component";
import React from "react";
import { render } from "react-dom";

const Store_Individual_Item = (props) => {
  const setCurrentItemImage = props.setCurrentItemImageProp;
  const setCurrentItemPrice = props.setCurrentItemPriceProp;
  const setCurrentItemDesc = props.setCurrentItemDescProp;
  const this_Item = props.this_Item_Prop;
  const this_Item_Image = props.imageProp;
  const this_Item_Price = props.priceProp;
  const this_Item_Title = props.titleProp;
  const this_Item_Desc = props.descProp;
  const index = props.Index;
  const setItemQty = props.setItemQtyProp;
  const setModalVisible = props.setModalVisibleProp;
  const addToCart = props.addToCartProp;

  const ratingChanged = (newRating) => {
    console.log(newRating);
  };

  return (
    <div className="ind_item_fade" key={index}>
      <p id="apparel_title">{this_Item_Title}</p>

      <select
        name="selectStock"
        className="item_size_input"
        onChange={(e) => {
          setItemQty(e.target.value);
        }}
      >
        <option value="null">Select Qty-</option>
        <option value="1">1</option>
        <option value="2">2</option>
        <option value="3">3</option>
      </select>
      <div id="item_img_holder">
        <img
          src={this_Item_Image}
          className="item_image"
          onClick={() => {
            setModalVisible(true);
            setCurrentItemImage(this_Item_Image);
            setCurrentItemPrice(this_Item_Price);
            setCurrentItemDesc(this_Item_Desc);
          }}
        ></img>

        <div id="skill_desc" class="desc_visibility">
          <h2 id="desc_title">{this_Item_Title}</h2>
          <div id="desc_bkrd">
            <p
              onClick={() => {
                setModalVisible(true);
                setCurrentItemImage(this_Item_Image);
                setCurrentItemPrice(this_Item_Price);
                setCurrentItemDesc(this_Item_Desc);
              }}
            >
              {this_Item_Desc}
            </p>
          </div>
        </div>
      </div>
      <Button
        variant="outline-primary"
        type="button"
        id="cart_add_btn"
        onClick={() => addToCart(this_Item)}
      >
        Add to Cart
      </Button>
      <ReactStars
        count={5}
        onChange={ratingChanged}
        size={24}
        isHalf={true}
        emptyIcon={<i className="far fa-star"></i>}
        halfIcon={<i className="fa fa-star-half-alt"></i>}
        fullIcon={<i className="fa fa-star"></i>}
        activeColor="#ffd700"
      />
      <p id="apparel_price">{this_Item_Price}</p>
    </div>
  );
};

export default Store_Individual_Item;
