import { combineReducers } from "redux";
import cart_Reducer from "./cart_Reducer";
import jwToken_Reducer from "./jwToken_Reducer";
import total_Reducer from "./total_Reducer";
import userID_Reducer from "./userID_Reducer";
import tax_Reducer from "./tax_Reducer";

const allReducers = combineReducers({
  userID: userID_Reducer,
  jwToken: jwToken_Reducer,
  cart: cart_Reducer,
  total: total_Reducer,
  tax: tax_Reducer,
});

export default allReducers;
